import React , {useState, useEffect } from 'react'
import { useHistory } from "react-router-dom";
import {Link} from 'react-router-dom';



const Login =()=>{

    const history = useHistory();

   

    const [Description, setDescrip] = useState()
    const [Title, setTitle] = useState();
    const [image, setImage] = useState();
    const [errors, setErr] = useState()
   

    const userLogin = async(e) =>{
      e.preventDefault();
        
        fetch("https://holy-warp-jasmine.glitch.me/projects/addProjects", {
 
             method: 'post',
             headers: {
                 'Content-type': 'application/json'
             },
 
             body:JSON.stringify({
               Description,
               Title,
               image
             })
        }).then((res) => res.json())
          .then( async (data)=> {
        
            console.log(data)
            setErr(data.msg)
            history.push("/Home")
            
 
          })  
          .catch((error)=>{
              console.error(error);
           
          })
         
     }

     const[projects, setProjects] = useState([])

     useEffect(()=>{

      const getVacs = async() =>{
    
        const url = 'https://holy-warp-jasmine.glitch.me/projects/getProjects'
        const response = await fetch(url)
        const data = await response.json()

       // console.log("Never felt this good doing something bro"+data[0].name)
        setProjects(data)
      }
  
      getVacs()
     },[])


     const handleClick = userId => {
      const requestOptions = {
        method: 'DELETE'
      };
    
      // Note: I'm using arrow functions inside the `.fetch()` method.
      // This makes it so you don't have to bind component functions like `setState`
      // to the component.
      fetch("https://holy-warp-jasmine.glitch.me/projects/delete/"+userId, requestOptions).then((response) => {
        return response.json();
      }).then((result) => {
              history.push("/Home")
      });
    }



        return(


            <div>
            <header id="mu-header">  
              <nav className="navbar navbar-default mu-main-navbar" role="navigation">  
                <div className="container">
                  <div className="navbar-header">
                    {/* FOR MOBILE VIEW COLLAPSED BUTTON */}
                    <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
                      <span className="sr-only">Toggle navigation</span>
                      <span className="icon-bar" />
                      <span className="icon-bar" />
                      <span className="icon-bar" />
                    </button>
                    {/* LOGO */}       
                    {/*  Text based logo  */}

                  
                    {/* <a className="navbar-brand" href="index.html"> <img src="https://i.ibb.co/vDTHnDn/logo.png" alt="Logo" /> </a>  */}

                    <Link to='/' class="navbar-brand" >
                       <img src="assets/img/logo.png" alt="Logo img"/>
                    </Link>
                    {/*  Image based logo  */}
                    {/* <a class="navbar-brand" href="index.html"><img src="assets/img/logo.png" alt="Logo img"></a>  */}
                  </div>
                  <div id="navbar" className="navbar-collapse collapse">
                    <ul id="top-menu" className="nav navbar-nav navbar-right mu-main-nav">
                      <li><Link  to='/Home' class="navbar-brand" >Home</Link></li>
                      <li><Link to='/Vacancies' >Vacancies</Link></li>
                      <li><Link to='/Tenders'>Tenders</Link></li>
                      <li className='active'><Link to='/Projects'>Community</Link></li>
                      <li><Link to='/Learnership'>Learnership</Link></li>                                                                           
                    </ul>                            
                  </div>{/*/.nav-collapse */}       
                </div>          
              </nav> 
            </header>

              
                <form className="form-signin" onSubmit={userLogin}>
              <img className="mb-4" src="https://i.ibb.co/tPGxBHG/logo.png" alt="" width={172} height={72} />
              <h1 className="h3 mb-3 font-weight-normal">Add New Project</h1>

               <label htmlFor="inputEmail" className="sr-only">Project Title</label>
                <input 
                    type="text" 
                    id="inputname"   
                    onChange={(e) => setDescrip(e.target.value)}
                    className="form-control" 
                    placeholder="Job Title" 
                    required autoFocus />

                <label htmlFor="inputname" className="sr-only">Description</label>
                <input 
                    type="text" 
                    id="inputname"   
                    onChange={(e) => setTitle(e.target.value)}
                    className="form-control" 
                    placeholder="Description"  
                    required autoFocus />

               <label htmlFor="inputname" className="sr-only">Image (URL)</label>
                <input 
                    type="text" 
                    id="inputname"   
                    onChange={(e) => setImage(e.target.value)}
                    className="form-control" 
                    placeholder="Image URL"  
                    required autoFocus />
               
             <div className="checkbox mb-3">
             </div>
             <h6 className=''>{errors}</h6>
            <button className="btn btn-lg btn-primary btn-block" type="submit">Add File</button>
             <p className="mt-5 mb-3 text-muted">© 2020</p>
              </form>
              
              {projects.map(p =>(
             <div>
             <div className="container-fluid">
              <div className="row">
                <div className="col-md-2">&nbsp;</div>
                <div className="col-md-8">
                  <div className="row space-16">&nbsp;</div>
                  <div className="row">

                  
                     <div className="col-sm-4">
                      <div className="thumbnailCom">
                        <div className="caption text-center">
                          <div className="thumbnail-description smaller">
                            <img alt="img" src={p.Image} />
                          </div>
                        </div>
                      </div>
                    </div>

                    <div className="col-sm-8">
                      <div className="thumbnailCom">
                        <div className="caption text-center">
                          <div className="thumbnail-description smaller">
                            <h3>{p.Title}</h3>
                            <p> {p.Description}</p>
                            <button color='red' onClick={() => { handleClick(p._id) }} className="button">Delete</button>
                          </div>
                        </div>
                      </div>
                    </div>
                      
                 </div>

                 
                  </div>
                  <div className="col-md-2">&nbsp;</div>
                </div>
              </div>
            </div> 
             ))}
            </div>
        
        )

        
       
        
    
}



export default Login