import React  from 'react'
import {Link} from 'react-router-dom';




class Home extends React.Component{

    render(){


        return(

          <div>

             <header id="mu-header">  
              <nav className="navbar navbar-default mu-main-navbar" role="navigation">  
                <div className="container">
                  <div className="navbar-header">
                    {/* FOR MOBILE VIEW COLLAPSED BUTTON */}
                    <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
                      <span className="sr-only">Toggle navigation</span>
                      <span className="icon-bar" />
                      <span className="icon-bar" />
                      <span className="icon-bar" />
                    </button>
                    {/* LOGO */}       
                    {/*  Text based logo  */}

                  
                    {/* <a className="navbar-brand" href="index.html"> <img src="https://i.ibb.co/vDTHnDn/logo.png" alt="Logo" /> </a>  */}

                    <Link to='/' class="navbar-brand" >
                       <img src="assets/img/logo.png" alt="Logo img"/>
                    </Link>
                    {/*  Image based logo  */}
                    {/* <a class="navbar-brand" href="index.html"><img src="assets/img/logo.png" alt="Logo img"></a>  */}
                  </div>
                  <div id="navbar" className="navbar-collapse collapse">
                    <ul id="top-menu" className="nav navbar-nav navbar-right mu-main-nav">
                      <li className="active"><Link  to='/Home' class="navbar-brand" >Home</Link></li>
                      <li><Link to='/Vacancies' >Vacancies</Link></li>
                      <li><Link to='/Tenders'>Tenders</Link></li>
                      <li><Link to='/Projects'>Community</Link></li>
                      <li><Link to='/Learnership'>Learnership</Link></li>                                                                           
                    </ul>                            
                  </div>{/*/.nav-collapse */}       
                </div>          
              </nav> 
            </header>
        {/* End header section */}
        {/* Carousel
    ================================================== */}
        <div id="myCarousel" className="carousel slide">
          <div className="carousel-inner" role="listbox">
            <div className="item active">
              <img className="first-slide" src="https://previews.123rf.com/images/
              everythingpossible/everythingpossible1809/everythingpossible180900435/107981072-
              intelligence-bi-and-business-analytics-ba-with-key-performance-indicators-kpi-dashboard-concept-busi.jpg" alt="First slide" />
              <div className="container">
                <div className="carousel-caption">
                </div>
              </div>
            </div>
          </div>
        </div>{/* /.carousel */}

          </div>

         

        )

       
       
    }
}



export default Home