import React , {useState , useEffect} from 'react'
import { useHistory } from "react-router-dom";
import {Link} from 'react-router-dom';


const Login =()=>{

    const history = useHistory();

   

    const [name, setName] = useState()
    const [file, setFile] = useState();
    const [errors, setErr] = useState()
   

    const userLogin = async(e) =>{
      e.preventDefault();
        
        fetch("https://holy-warp-jasmine.glitch.me/Tenders/Tenders", {
 
             method: 'post',
             headers: {
                 'Content-type': 'application/json'
             },
 
             body:JSON.stringify({
               name,
               file
             })
        }).then((res) => res.json())
          .then( async (data)=> {
        
            console.log(data)
            setErr(data.msg)
            history.push("/Home")
            
 
          })  
          .catch((error)=>{
              console.error(error);
           
          })
         
     }

     const[projects, setProjects] = useState([])

     useEffect(()=>{

      const getVacs = async() =>{
    
        const url = 'https://holy-warp-jasmine.glitch.me/Tenders/Tenders'
        const response = await fetch(url)
        const data = await response.json()

       // console.log("Never felt this good doing something bro"+data[0].name)
        setProjects(data)
      }
  
      getVacs()
     },[])
  

    const handleClick = userId => {
      const requestOptions = {
        method: 'DELETE'
      };
    
      // Note: I'm using arrow functions inside the `.fetch()` method.
      // This makes it so you don't have to bind component functions like `setState`
      // to the component.
      fetch("https://holy-warp-jasmine.glitch.me/Tenders/delete/"+userId, requestOptions).then((response) => {
        return response.json();
      }).then((result) => {
              history.push("/Home")
      });
    }

        return(


            <div>

            <header id="mu-header">  
              <nav className="navbar navbar-default mu-main-navbar" role="navigation">  
                <div className="container">
                  <div className="navbar-header">
                    {/* FOR MOBILE VIEW COLLAPSED BUTTON */}
                    <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
                      <span className="sr-only">Toggle navigation</span>
                      <span className="icon-bar" />
                      <span className="icon-bar" />
                      <span className="icon-bar" />
                    </button>
                    {/* LOGO */}       
                    {/*  Text based logo  */}

                  
                    {/* <a className="navbar-brand" href="index.html"> <img src="https://i.ibb.co/vDTHnDn/logo.png" alt="Logo" /> </a>  */}

                    <Link to='/' class="navbar-brand" >
                       <img src="assets/img/logo.png" alt="Logo img"/>
                    </Link>
                    {/*  Image based logo  */}
                    {/* <a class="navbar-brand" href="index.html"><img src="assets/img/logo.png" alt="Logo img"></a>  */}
                  </div>
                  <div id="navbar" className="navbar-collapse collapse">
                    <ul id="top-menu" className="nav navbar-nav navbar-right mu-main-nav">
                      <li><Link  to='/Home' class="navbar-brand" >Home</Link></li>
                      <li><Link to='/Vacancies' >Vacancies</Link></li>
                      <li className="active"><Link to='/Tenders'>Tenders</Link></li>
                      <li><Link to='/Projects'>Community</Link></li>
                      <li><Link to='/Learnership'>Learnership</Link></li>                                                                           
                    </ul>                            
                  </div>{/*/.nav-collapse */}       
                </div>          
              </nav> 
            </header>
                <form className="form-signin" onSubmit={userLogin}>
              <img className="mb-4" src="https://i.ibb.co/tPGxBHG/logo.png" alt="" width={172} height={72} />
              <h1 className="h3 mb-3 font-weight-normal">Add new Tender</h1>

               <label htmlFor="inputEmail" className="sr-only">Tender Description</label>
                <input 
                    type="text" 
                    id="inputname"   
                    onChange={(e) => setName(e.target.value)}
                    className="form-control" 
                    placeholder="Job Title" 
                    required autoFocus />

                <label htmlFor="inputname" className="sr-only">File URL</label>
                <input 
                    type="text" 
                    id="inputname"   
                    onChange={(e) => setFile(e.target.value)}
                    className="form-control" 
                    placeholder="File URL"  
                    required autoFocus />
               
             <div className="checkbox mb-3">
             </div>
             <h6 className=''>{errors}</h6>
            <button className="btn btn-lg btn-primary btn-block" type="submit">Add File</button>
             <p className="mt-5 mb-3 text-muted">© 2020</p>
              </form>


              <div className="container">
              <h1 style={{color: 'black'}}><b>Open Tenders</b></h1>


              {projects.length === 0 ?
                 <div>
                     <div className="box">
                     <h4>No Tenders Open</h4>
                     </div>
                 </div> :
                 
                 
                 <div>
                   {projects.map(p=>(

                     <div className="box">

                   <div className="box-col box-80">
                    <div>
                        <h4>{p.name}</h4>
                        <div className="box-col box-20 text-right">
                       <a class="button" href={p.file} target="_blank">Download PDF file</a>
                       
                       <button color='red' onClick={() => { handleClick(p._id) }} className="button">Delete</button>
                     </div> 
                    </div>
                 

                </div> 
              </div>
               ))}

                 </div>
              }
             
            </div>  
            </div>
        
        )

        
       
        
    
}



export default Login